import axios from "axios";
import {GET_MARKETS_URL} from "../constants/urls";

export default {
    data() {
        return {
            markets: []
        }
    },
    methods: {
        getMarkets(vSelectFormat = true) {
            return axios.get(GET_MARKETS_URL)
                .then(response => {
                    if (vSelectFormat) {
                        this.markets = response.data.data.map(market => {
                            return {
                                value: market.id,
                                label: market.name
                            }
                        })
                    }else{

                        this.markets = response.data;
                    }
                })
        }
    }


}