<template>
    <div>
        <form @submit.prevent="submit">
            <div class="row">

                <form-group label="Título" :validate="vee" name="title">
                    <input class="form-control" v-model="program.title" v-validate="'required|max:100'" name="title"/>

                </form-group>

                <form-group label="Descrição" name="description" :validate="vee">
                    <input class="form-control" v-model="program.description" v-validate="'required|max:191'"
                           name="description"/>
                </form-group>

                <form-group label="Sobre" name="about" :validate="vee">
                    <wysiwyg v-model="program.about" refs="about" v-validate="'required'" name="about"/>
                </form-group>

                <form-group label="Data de início" col="col-md-6" name="program_start" :validate="vee">
                    <datetime type="datetime" input-class="form-control" v-model="program.program_start"
                              v-validate="'required'" name="program_start"></datetime>
                    <span class="text-danger" v-if="errors" v-for="e in errors.program_start">
                    <small>{{e}}</small>
                </span>
                </form-group>

                <!-- DATES -->
                <form-group label="Data de encerramento" col="col-md-6" :validate="vee" name="program_end">
                    <datetime type="datetime" input-class="form-control" v-model="program.program_end"
                              v-validate="'required'"
                              name="program_end"/>
                    <span class="text-danger" v-if="errors" v-for="e in errors.program_end">
                    <small>{{e}}</small>
                </span>
                </form-group>

                <form-group label="Data de início das incrições" col="col-md-6" :validate="vee" name="date_start">
                    <datetime type="datetime" input-class="form-control" v-model="program.date_start"
                              :disabled="disableSecondeDates"
                              v-validate="'required'" placeholder="Defina as datas de início e fim"
                              name="date_start"/>
                    <span class="text-danger" v-if="errors" v-for="e in errors.date_start">
                    <small>{{e}}</small>
                </span>
                </form-group>

                <form-group label="Data do fim das incrições" col="col-md-6" :validate="vee" name="date_end">
                    <datetime type="datetime" input-class="form-control" v-model="program.date_end"
                              :disabled="disableSecondeDates"
                              v-validate="'required'" placeholder="Defina as datas de início e fim"
                              name="date_end"/>
                    <span class="text-danger" v-if="errors" v-for="e in errors.date_end">
                    <small>{{e}}</small>
                </span>
                </form-group>
                <!-- END DATES -->


                <form-group label="Estado" col="col-md-6" name="state_id" :validate="vee">
                    <v-select v-model="program.state_id" :options="states" v-validate="'required'"
                              name="state_id"></v-select>
                    <span class="text-danger" v-if="errors" v-for="e in errors.state_id">
                    <small>{{e}}</small>
                </span>
                </form-group>

                <form-group label="Áreas de atuação" col="col-md-6" :validate="vee" name="markets">
                    <v-select v-model="program.markets" :options="markets" name="markets" multiple
                              v-validate="'required'"></v-select>
                    <span class="text-danger" v-if="errors" v-for="e in errors.markets">
                    <small>{{e}}</small>
                </span>
                </form-group>

                <form-group label="Imagem" name="cover" :validate="vee">
                    <input type="file" class="form-control" name="cover" @change="fileChange" ref="cover"
                           v-validate="'required'"/>
                    <span class="text-danger" v-if="errors" v-for="e in errors.cover">
                    <small>{{e}}</small>
                </span>
                </form-group>

                <!--<img :src="imagePreview" class="img-fluid"/>-->
                <form-group>
                    <div class="row">
                        <div class="col-md-6">
                            <a class="btn btn-warning " @click.prevent="$modal.show('modal-form-builder')"
                               v-if="!formBuilded">
                                Cadastrar ficha de inscrição <i class="os-icon os-icon-window-content"></i></a>
                            <a class="btn btn-warning " @click.prevent="modalOpen=true" v-else>
                                Editar ficha de inscrição <i class="os-icon os-icon-window-content"></i>
                            </a>
                            <span class="text-danger" v-if="errors" v-for="e in errors.form_custom">
                            <small>{{e}}</small>
                                    </span>
                        </div>
                        <div class="col-md-6">
                            <button type="submit" class="btn btn-primary float-right">Enviar</button>
                        </div>
                    </div>
                </form-group>

            </div>
        </form>

        <modal name="modal-form-builder" :adaptive="true" width="80%" height="auto" :scrollable="true"
               @before-close="onCloseModal" v-show="modalOpen" @before-open="formBuilded = modalOpen = true">
            <div class="container">
                <div class="row">
                    <div class="col-md-12 text-right">
                        <a href="#" @click.prevent="modalOpen=false" class="pull-right btn-close-modal">
                            <i class="os-icon os-icon-close"></i>
                        </a>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12">
                        <form-builder @saveData="saveFormBuilder"></form-builder>
                    </div>
                </div>
            </div>
        </modal>


    </div>
</template>
<script>

    import handleErrors from '../../../mixins/handle_errors';
    import cities_state_mixins from '../../../mixins/cities_states';
    import marketsMixins from '../../../mixins/markets';
    import dateMixins from '../../../mixins/dates';
    import FormBuilder from '../../../components/FormBuilder';
    import {DateTime} from 'luxon';
    import watchers from './watchers'


    export default {
        mixins: [cities_state_mixins, marketsMixins, dateMixins, handleErrors],
        components: {
            FormBuilder
        },
        data() {
            return {
                program: {},
                markets: [{}],
                imageUpload: '',
                imagePreview: '',
                errors: {},
                modalOpen: false,
                formBuilded: false,
                disableSecondeDates: true
            }
        },
        methods: {
            /*
            * return int
            * return the timestamp of a date string
            * */
            ts(d) {
                return DateTime.fromISO(d).ts;
            },
            onCloseModal(e) {
                e.stop();
            },
            createProgram() {
                this.preload(true);
                let data = this.getFormData();
                this.$store.dispatch('createProgram', data).then(
                    response => {
                        this.$router.push({name: 'admin.program.index'});
                        this.$snotify.success(this.constants.messages.CREATE_SUCCESS_MESSAGE, this.constants.messages.SUCCESS_MESSAGE);
                    }
                ).catch((e) => {
                    this.handleErrors(e)
                }).finally(() => {
                    this.preload(false);
                });
            },
            saveFormBuilder(data) {
                this.modalOpen = false;
                this.program.form_custom = data;
            },

            submit() {
                this.$validator.validate().then((hasnotError) => {
                    if (hasnotError) {
                        this.createProgram();
                    } else {
                        this.$snotify.warning(this.constants.messages.FORM_VALIDATE_MESSAGE);
                    }
                })
            },
            resetForm() {
                return {
                    title: '',
                    description: '',
                    about: '',
                    program_start: "",
                    program_end: "",
                    date_start: "",
                    date_end: "",
                    state_id: '',
                    markets: [],
                    cover: '',
                    form_custom: null
                }
            },
            getFormData: function () {

                let data = new FormData();
                let markets = this.program.markets.map(o => o.value);

                for (var i = 0; i < markets.length; i++) {
                    data.append('markets[]', markets[i]);
                }

                data.append('title', this.program.title);
                data.append('description', this.program.description);
                data.append('about', this.program.about);
                data.append('program_start', this.formatDateFromIsoToSend(this.program.program_start));
                data.append('program_end', this.formatDateFromIsoToSend(this.program.program_end));
                data.append('date_end', this.formatDateFromIsoToSend(this.program.date_end));
                data.append('date_start', this.formatDateFromIsoToSend(this.program.date_start));
                data.append('state_id', this.program.state_id.value);

                data.append('form_custom', this.program.form_custom);

                this.imageUpload && data.append("cover", this.imageUpload);

                return data;

            },

            fileChange(event) {
                this.errors.cover = "";

                let files = event.target.files || event.dataTranfer.files;

                if (files.length) {

                    let file = files[0];

                    if (file.size > this.constants.DEFAULT_IMAGE_UPLOAD_SIZE) {

                        this.errors.cover = "o tamanho da imagem excedeu o tamanho máximo";

                        this.$refs.cover.value = ""

                    } else {

                        this.imageUpload = file;

                        this.setImage(this.imageUpload);

                    }

                } else {
                    this.$snotify.error("Não há arquivos selecionados", "Erros!");
                }
            },
            setImage(file) {

                let reader = new FileReader();

                reader.readAsDataURL(file);

                reader.onload = e => {
                    this.imagePreview = e.target.result;
                };

            },

        },
        mounted() {

            this.getStates();
            this.getMarkets();

        },
        created() {
            this.program = this.resetForm();

        },
        watch: watchers
    }
</script>
<style lang="scss" scoped>

    $border-radius-default: 4px;

    #modal-form-builder {
        padding: 40px 50px 40px 50px;
    }

    .editr {
        border-radius: $border-radius-default;
    }

    .editr--toolbar {
        border-radius: $border-radius-default $border-radius-default 0 0;
    }

    .btn-close-modal {
        margin: 25px;
        text-decoration: none;
    }

    .container {
        padding-top: 25px;
    }
</style>