<template>
    <div >
        <div id="fb-editor"></div>
    </div>
</template>
<script>

    require('formBuilder');
    require('jquery-ui-sortable');
    import {ptBRLang} from '../plugins/FormBuilder'

    export default {
        components: {},
        props:{
          defaultFields:{
              type:Array,
          }
        },
        data: function () {
            return {
                componentMounted:false,
                form: "",
                options: {

                    stickyControls: {
                        enable: true,
                        offset: {
                            top: 20,
                            right: 20,
                            left: 'auto'
                        }
                    },
                    i18n: {
                        preloaded: {
                            'pt-BR': ptBRLang,
                        },
                        locale: 'pt-BR',
                    },

                    actionButtons: [
                        {
                            id: 'salvar',
                            className: 'btn btn-success',
                            label: 'Salvar',
                            type: 'button',
                            events: {
                                click: () => {
                                    this.saveData();

                                }
                            }
                        }
                    ],
                    roles: {},
                    disableFields: ['autocomplete', 'hidden', 'button'],
                    defaultFields: [],


                    // fieldRemoveWarn: true,
                    disabledAttrs: [
                        'access'
                        // 'name','role
                    ],
                    disabledActionButtons: ['data', 'save', 'clear']
                }
            }
        },
        methods:
            {
                saveData() {
                    this.$emit('saveData', this.form.actions.getData('json'));
                },
                mountFormBuider() {
                    this.componentMounted =true;
                    this.form = $(document.getElementById('fb-editor')).formBuilder(this.options);

                }
            }
        ,
        mounted() {
            this.mountFormBuider();
            /*_*/
            /* this saves when field is added */
            // document.addEventListener('fieldAdded', () => {
            //     this.saveData()
            // });
        }
    }
</script>
<style lang="scss">
    #form-builder-wrapper{
        width: 100%;
    }
    #fb-editor {
        .stage-wrap.pull-left ul li {
            height: 100%;
        }
        #frmb-1539114382176-cb-wrap > div {
            color: white;
        }

        .form-actions button {
            color: white;
        }
        margin: 50px;
    }
</style>