import {DateTime} from 'luxon';

export default {
    'program.program_start': function (e) {
        // this.program.date_start = this.program.program_start;
        this.program.program_start && this.program.program_end ? this.disableSecondeDates = false : true;

        /*ao mudar a data de inicio se a data início for apos a data do fim adciona 1 dia a data inicio e atribui a data fim*/
        if (this.ts(this.program.program_start) > this.ts(this.program.program_end) && this.program.program_end) {
            let date = DateTime.fromISO(this.program.program_start);
            this.program.program_end = date.plus({days: 1}).toString();
        }

        if (this.ts(this.program.program_start) > this.ts(this.program.date_start) && this.program.date_start) {
            this.program.date_start = this.program.program_start;
        }
    },
    'program.program_end': function (e) {
        this.program.program_start && this.program.program_end ? this.disableSecondeDates = false : true;
        // this.program.date_end = this.program.program_end;

        // ao mudar a data de fim se a data fim for anterior a data do inicio adciona 1 dia a data inicio e atribui a data fim*/
        if (this.ts(this.program.program_end) < this.ts(this.program.program_start) && this.program.program_start) {
            let date = DateTime.fromISO(this.program.program_end);
            this.program.program_start = date.minus({days: 1}).toString()
        }
        /*se a data do fim do programa for menor que a data do fim do programa muda*/
        if (this.ts(this.program.program_end) < this.ts(this.program.date_start) && this.program.date_start) {
            this.program.date_start = this.program.program_end;
        }

        /*se o fim do programa for menor que o fim das inscriçoes muda*/
        if (this.ts(this.program.program_end) < this.ts(this.program.date_end) && this.program.date_end) {
            this.program.date_end = this.program.program_end;
        }


    },
    'program.date_start': function (e) {
        if (
            this.ts(this.program.date_start) > this.ts(this.program.program_end) ||
            this.ts(this.program.date_start) < this.ts(this.program.program_start) ||
            this.ts(this.program.date_start) > this.ts(this.program.date_end)
        ) {
            this.program.program_start = this.program.date_start;
        }

        /*se o inicio das inscriçoes for maior que o fim muda*/
        if (this.ts(this.program.date_start) > this.ts(this.program.date_end) && this.program.date_end) {
            this.program.date_end = this.program.date_start;
        }

        // se o fim das inscriçoes form maior qu a data de encerramento
        if (this.ts(this.program.date_end) > this.ts(this.program.program_end)) {
            this.program.program_end = this.program.date_end;
        }


    },
    'program.date_end': function (e) {
        if (
            this.ts(this.program.date_end) < this.ts(this.program.program_start) ||
            this.ts(this.program.date_end) < this.ts(this.program.date_start) ||
            this.ts(this.program.date_end) > this.ts(this.program.program_end)
        ) {
            this.program.program_end = this.program.date_end;
        }
    }
}
